/* src/App.css */
.App {
  display: flex;
  flex-direction: column;
  height: 100vh;
  font-family: Arial, sans-serif;
}

header {
  padding: 20px;
  background-color: #f4f4f4;
  border-bottom: 1px solid #ddd;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.legend {
  margin-top: 10px;
  text-align: left;
}

.legend h2 {
  margin: 10px 0;
}

.legend .info-text {
  font-size: 14px;
  color: #555;
  margin-bottom: 10px;
}

.legend ul {
  list-style: none;
  padding: 0;
}

.legend li {
  margin: 5px 0;
}

.main-content {
  display: flex;
  flex: 1;
}

.sidebar {
  display: flex;
  flex-direction: column;
  width: 220px;
  padding: 20px;
  background-color: #e0f7fa;
  border-right: 1px solid #ddd;
  overflow-y: auto;
}

.fields {
  display: flex;
  flex-direction: column;
  width: 220px;
  padding: 20px;
  background-color: #e1bee7;
  border-right: 1px solid #ddd;
  overflow-y: auto;
}

.services label, .fields label {
  font-weight: bold;
  margin-bottom: 10px;
  display: block;
}

.checkbox-container {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  cursor: pointer;
}

.checkbox-container input[type="checkbox"] {
  margin-right: 10px;
}

.checkbox-container span {
  cursor: pointer;
  display: block;
  width: 100%;
}

.checkbox-container:hover {
  background-color: #f0f0f0;
}

.chart-container {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  background-color: #fafafa;
}

.loading-indicator {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

@media (max-width: 768px) {
  .main-content {
      flex-direction: column;
  }

  .sidebar, .fields {
      width: 100%;
  }

  .chart-container {
      padding: 10px;
  }

  .checkbox-container {
      flex-direction: column;
      align-items: flex-start;
  }

  .checkbox-container input[type="checkbox"] {
      margin-bottom: 5px;
  }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
   100% { transform: rotate(360deg); }
}
